/*font*/

//3D文本
@mixin text3d($color) {
    color: $color;
    text-shadow: 1px 1px 0px darken($color, 5%),
    2px 2px 0px darken($color, 10%),
    3px 3px 0px darken($color, 15%),
    4px 4px 0px darken($color, 20%),
    4px 4px 2px #000;
}

.bold {
    font-weight: bold;
}

.italic {
    font-style: italic;
}

.f0 {
    font-size: 0;
}

.f10 {
    font-size: 10px;
    font-weight: normal;
}

